import { Component, OnInit, Inject } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { DOCUMENT } from '@angular/common';

import { ZoomMtg } from '@zoomus/websdk';

ZoomMtg.preLoadWasm();
ZoomMtg.prepareJssdk();

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent implements OnInit {

  // setup your signature endpoint here: https://github.com/zoom/websdk-sample-signature-node.js
 
  zoomSettings:any = {};
  constructor(public httpClient: HttpClient, @Inject(DOCUMENT) document) {
    
    httpClient.get("/Home/getConfig")
      .subscribe(function(res){

      }, function(res){

      });
  }

  ngOnInit() {

  }

  getSignature() {
    this.startMeeting(this.zoomSettings.signature);
  }
  startMeeting(signature) {

    document.getElementById('zmmtg-root').style.display = 'block'

    ZoomMtg.init({
      leaveUrl: this.zoomSettings.leaveUrl,
      isSupportAV: true,
      success: (success) => {
        console.log(success)

        ZoomMtg.join({
          signature: signature,
          meetingNumber: this.zoomSettings.meetingId,
          userName: "Angular",
          apiKey: this.zoomSettings.apiKey,
          userEmail: "shiv@zoom.com",
          passWord: this.zoomSettings.password,
          success: (success) => {
            console.log(success)
          },
          error: (error) => {
            console.log(error)
          }
        })

      },
      error: (error) => {
        console.log(error)
      }
    })
  }
}
